<template>
  <div class="about">
    <v-container>
      <v-btn text @click="goBack"> <span class="material-icons">keyboard_arrow_left</span>Back to Results </v-btn>
      <v-layout row wrap>
        <v-col md="auto">
          <h2>{{ selectedProvider.Prov_Name }}, {{ selectedProvider.Degree }}</h2>
        </v-col>
        <v-col>
          <div v-if="selectedProvider.Provider_Type.toLowerCase() === 'captive'">
            <v-img alt="CHSLI Logo" class="shrink mr-2" contain src="https://www.chsli.org/themes/custom/chsli/logo.png" transition="scale-transition" width="120" style="float:left;" />
            <span class="employed">CHS Physician Partners Medical Group</span>
          </div>
        </v-col>
      </v-layout>
      <hr />
      <v-layout row wrap>
        <v-col cols="12" sm="6" md="4" order-sm="2" order-md="2">
          <v-img error="imgLoadError" width="200" contain v-bind:src="`${imagePath}/${selectedProvider.NPI}.jpg`"></v-img>
        </v-col>
        <v-col xs="12" sm="6" md="8" order-sm="1" order-md="1">
          <v-row>
            <v-col col-10>
              <h2><i class="fas fa-stethoscope chsli-icon"></i>Specialty</h2>
              <div>{{ selectedProvider.Speciality }}</div>
            </v-col>
            <v-col col-2>
              <h2 style="text-align:center">{{ selectedProvider.Tier }}</h2>
            </v-col>
          </v-row>
          <v-row>
            <v-col col-12>
              <h2><i class="fas fa-hospital chsli-icon"></i>Hospital Affiliations</h2>
              <div v-for="(aff, index) in selectedProvider.Hospital_Affiliations" v-bind:key="index">
                <div>{{ aff.Hospital_Name }}</div>
              </div>
            </v-col>
          </v-row>
        </v-col>
      </v-layout>
      <v-layout row wrap>
        <v-col col-12>
          <h2><i class="fas fa-graduation-cap chsli-icon"></i>Education and Training</h2>
          <div v-for="(ed, index) in selectedProvider.Institutions" v-bind:key="index">
            <div>{{ ed.Type }}, {{ ed.Institution_Name }}, {{ ed.City }}, {{ ed.State }}</div>
          </div>
        </v-col>
      </v-layout>
      <v-layout row wrap>
        <v-col cols="12">
          <h2><i class="fas fa-map-marker-alt chsli-icon"></i>Addresses</h2>
          <v-layout row wrap>
            <v-col md="4" sm="6" cols="12" v-for="(loc, index) in selectedProvider.Location" v-bind:key="index">
              <v-card color="#f1ebe5">
                <v-card-text>
                  <div>{{ loc.Practice }}</div>
                  <div>{{ loc.Address }}</div>
                  <div>{{ loc.City }}, {{ loc.State }} {{ loc.Zip }}</div>
                  <div>
                    <i class="fas fa-phone chsli-icon"></i>
                    {{ loc.Phone }}
                  </div>
                  <div>
                    <i class="fas fa-fax chsli-icon"></i>
                    {{ loc.Fax }}
                  </div>
                  <!-- <span
                    v-if="loc.Longitude !== '' && loc.Latitude !== ''"
                    class="material-icons"
                    :title="`Lng - ${loc.Longitude}/ Lat - ${loc.Latitude}`"
                  >location_on</span>-->
                </v-card-text>
              </v-card>
            </v-col>
          </v-layout>
        </v-col>
      </v-layout>
      <v-layout row wrap>
        <v-col xs="12">
          <GmapMap ref="gmap" :center="defaultCenter" :zoom="15" map-type-id="roadmap" style="width: 100%; height: 400px">
            <gmap-info-window :options="infoOptions" :position="infoWindowPos" :opened="infoWinOpen" @closeclick="infoWinOpen = false"></gmap-info-window>
            <GmapMarker :key="index" v-for="(m, index) in markers" :position="m.position" title="label" :clickable="true" :draggable="true" @click="toggleInfoWindow(m, index)" />
          </GmapMap>
        </v-col>
      </v-layout>
      <v-btn text @click="goBack"> <span class="material-icons">keyboard_arrow_left</span>Back to Results </v-btn>
    </v-container>
  </div>
</template>

<script>
import fullConfig from "../config";

const Config = fullConfig[process.env.NODE_ENV];

export default {
  name: "Provider",
  data: function() {
    return {
      infoWinOpen: false,
      infoWindowPos: {},
      infoOptions: {
        content: "",
        //optional: offset infowindow so it visually sits nicely on top of our marker
        pixelOffset: {
          width: 0,
          height: -35,
        },
      },
      selectedProvider: this.$route.params.selectedProvider,
      imagePath: Config.imageUrl,
    };
  },
  computed: {
    defaultCenter: function() {
      // see if one of the addresses has a valid position
      // if no, default
      for (let i = 0; i < this.markers.length; i++) {
        if (this.markers[i].position.lat != "" && this.markers[i].position.lng != "") {
          return {
            lat: parseFloat(this.markers[i].position.lat),
            lng: parseFloat(this.markers[i].position.lng),
          };
        }
      }

      return { lat: 40.7891, lng: -73.135 };
    },
    markers: function() {
      if (this.selectedProvider.Location) {
        return this.selectedProvider.Location.filter((loc) => loc.Latitude != "" && loc.Longitude != "").map((loc) => {
          return {
            position: {
              lat: parseFloat(loc.Latitude),
              lng: parseFloat(loc.Longitude),
            },
            infoText: `<strong>${loc.Practice}</strong>`,
          };
        });
      } else {
        return [];
      }
    },
    // selectedProvider: function() {
    //   return this.$store.getters.PROVIDER_LIST.find((el) => el.NPI === this.npi);
    // },
  },
  watch: {
    "$route.params": {
      // check for new params
      handler(newValue) {
        this.selectedProvider = newValue.selectedProvider;
      },
      immediate: true,
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.gmap.$mapPromise.then(() => {
        // this.$refs.gmap.$mapObject.fitBounds(this.googleMapBounds);
        this.generateBounds("mounted");
      });
    });
  },
  methods: {
    toggleInfoWindow: function(marker, idx) {
      // when clicking on map pin, display popup
      this.infoWindowPos = marker.position;
      this.infoOptions.content = marker.infoText;

      //check if its the same marker that was selected if yes toggle
      if (this.currentMidx == idx) {
        this.infoWinOpen = !this.infoWinOpen;
      }
      //if different marker set infowindow to open and reset current marker index
      else {
        this.infoWinOpen = true;
        this.currentMidx = idx;
      }
    },
    imgLoadError() {
      console.log("no image");
    },
    generateBounds(fromWhere) {
      if (this.markers.length > 1) {
        const bounds = new google.maps.LatLngBounds();
        for (let m of this.markers) {
          bounds.extend(m.position);
        }
        this.$refs.gmap.$mapObject.fitBounds(bounds);
      }
    },
    goBack: function() {
      this.$router.back();
    },
  },
};
</script>

<style>
.chsli-icon {
  margin-right: 8px;
  font-size: 20px;
}

.p-h2 {
  display: block;
  font-size: 1.5em;
  font-weight: bold;
}
</style>
